.btn {
	display: inline-block;
	font-weight: 400;
	color: rgb(33, 37, 41);
	text-align: center;
	vertical-align: middle;
	cursor: pointer;
	user-select: none;
	background-color: transparent;
	font-size: 1rem;
	line-height: 1.5;
	border-width: 1px;
	border-style: solid;
	border-color: transparent;
	border-image: initial;
	padding: 0.375rem 0.75rem;
	border-radius: 0.25rem;
	transition: color 0.15s ease-in-out 0s, background-color 0.15s ease-in-out 0s, border-color 0.15s ease-in-out 0s,
		box-shadow 0.15s ease-in-out 0s;
}

@media (min-width: 576px) {
	.container {
		max-width: 540px;
	}
	.col-sm-6 {
		max-width: 50%;
		flex: 0 0 50%;
	}
	.col-sm-12 {
		max-width: 100%;
		flex: 0 0 100%;
	}
}

@media (min-width: 768px) {
	.container {
		max-width: 720px;
	}
	.col-md-2 {
		max-width: 16.6667%;
		flex: 0 0 16.6667%;
	}
	.col-md-8 {
		max-width: 66.6667%;
		flex: 0 0 66.6667%;
	}
	.order-md-1 {
		order: 1;
	}
	.order-md-2 {
		order: 2;
	}
}
@media (min-width: 992px) {
	.container {
		max-width: 960px;
	}
}
@media (min-width: 1200px) {
	.container {
		max-width: 1140px;
	}
}


@keyframes border-pulse {
	0% {
		box-shadow: 0px 0px 15px 0px rgba(138, 33, 33, 0.575);
	}
	50% {
		box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0);
	}
	100% {
		box-shadow: 0px 0px 15px 0px rgba(138, 33, 33, 0.575);
	}
}

@keyframes flashing {
	0% {
		-webkit-transform: scale(0);
		transform: scale(0);

		opacity: 0.0;
	}
	20% {
		-webkit-transform: scale(0.2);
		transform: scale(0.2);
		opacity: 0.2;
	}
	40% {
		-webkit-transform: scale(0.5);
		transform: scale(0.5);
		opacity: 0.5;
	}
	60% {
		-webkit-transform: scale(0.7);
		transform: scale(0.7);
		opacity: 0.7;
	}
	80% {
		-webkit-transform: scale(0.9);
		transform: scale(0.9);
		background-color: rgba(85, 85, 85, 0.247);
		opacity: 0.2;
	}
	100% {
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
		opacity: 0.0;
	}
}

