	width: 100%;
	min-height: 5.75rem;
	border-radius: .25rem;
	position: relative;
}

.quotes.active {
	box-shadow: 0 0 3px 0.15rem rgb(23, 168, 27);
	outline: 0;
}

.quotes.active.is-error {
	box-shadow: 0 0 3px 0.15rem rgb(190, 25, 25);
	outline: 0;
}

